.buttonDonate {
  padding: 0.6em 2em;
  border: none;
  outline: none;
  color: rgb(255, 255, 255);
  background: #111;
  /* background-color: rgb(249, 105, 14); */
  position: relative;
  z-index: 0;
  border-radius: 10px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  cursor: pointer;
  animation: rota 2s linear infinite;
}

.buttonDonate:hover{
  animation: none;
}
@keyframes rota{
  50%{
    rotate: 2deg;
  }
  100%{
    rotate: -0.01turn;
  }
}

.buttonDonate:before {
  content: "";
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #7a00ff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  -webkit-filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing-button 20s linear infinite;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

@keyframes glowing-button {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

.buttonDonate:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #222;
  left: 0;
  top: 0;
  border-radius: 10px;
}

.bankDetails small{
  float: left;
  margin-top:  -30px;
}

#upi{
  font-size: 16px;
}

@media (max-width: 463px){
  #upi{
    font-size: 12px;
  }
}

.bankDetails{
  font-size: 15px;
}

@media (max-width: 379px){
  .bankDetails{
    font-size: 10px;
  }
}